/**
 * RetirementMainContainer.js
 * The main container for retirement
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import window from 'global/window';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

import RetirementMain from '../../components/Retirement/RetirementMain';

import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import { getRetirementFields, clearRetirementData } from '../../actions/retirementActions';
import { setLoading, clearLoading } from '../../actions/loadingScreenActions';
import { fbTag, gaTag, ampTag } from '../../lib/tagFunctions';

class RetirementMainContainer extends Component {
  constructor(props) {
    super(props);

    const {
      loading,
      retirementFields,
      history,
      clearData,
    } = this.props;

    clearData();

    if (!retirementFields) {
      loading(() => history.push('/'));
      this.props.getRetirementFields();
    }
  }

  componentDidMount() {
    const { setFormState, history } = this.props;
    setFormState(FORMSTATES.MAIN.index);

    window.scrollTo(0, 0);

    fbTag('PPS2 - Pageview - RetirementPage', { Product: 'Retirement' });
    gaTag({
      page_title: 'Retirement',
      page_path: `/retirement${history.location.search}`,
    });
    ampTag('RetireFormS');
  }

  componentDidUpdate(prevProps) {
    const { retirementFields, loadingEnd } = this.props;
    if (!prevProps.retirementFields && retirementFields) {
      loadingEnd();
    }
  }

  render() {
    const { retirementFields, retirementPromoGiveAway } = this.props;

    if (!retirementFields) {
      return null;
    }

    return (
      <RetirementMain
        retirementFields={retirementFields}
        retirementPromoGiveAway={retirementPromoGiveAway}
      />
    );
  }
}

RetirementMainContainer.defaultProps = {
  retirementFields: null,
  retirementPromoGiveAway: null,
};

RetirementMainContainer.propTypes = {
  retirementFields: PropTypes.object,
  getRetirementFields: PropTypes.func.isRequired,
  loading: PropTypes.func.isRequired,
  loadingEnd: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
  retirementPromoGiveAway: PropTypes.object,
  clearData: PropTypes.func.isRequired,
  setFormState: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  retirementFields: state.retirement.retirementFields,
  retirementPromoGiveAway: state.insurance.promoGiveaways.retirement,
});

const mapDispatchToProps = dispatch => ({
  getRetirementFields: () => {
    dispatch(getRetirementFields());
  },
  loading: (callBack, errorMsg, time) => {
    dispatch(setLoading(callBack, errorMsg, time));
  },
  loadingEnd: () => {
    dispatch(clearLoading());
  },
  clearData: () => {
    dispatch(clearRetirementData());
  },
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(RetirementMainContainer));
